<template>
  <div :class="{ 'plain-input--error': isError, disabled, focused: isFocused }" class="plain-input">
    <label :for="name" class="plain-input__label">
      {{ label }}
    </label>
    <div v-if="type !== 'textarea'" class="wrapper">
      <span v-if="isPhone" class="wrapper__phone-caret" @click="$refs.inputField.focus()" />
      <input
        :id="name"
        ref="inputField"
        :type="inputType"
        :value="value"
        v-bind="$attrs"
        :name="name"
        :required="required"
        :disabled="disabled"
        :aria-label="label"
        class="plain-input__input"
        @input="$emit('input', $event.target.value)"
        @change="$emit('change', $event.target.value)"
        @focus="onFocus"
        @blur="bluredEvent"
      />
      <icon
        v-if="type === 'password'"
        :name="isPassword ? 'eye' : 'eyeOff'"
        class="icon"
        size="22"
        @click="toggleType"
      />
    </div>
    <textarea
      v-else
      ref="textarea"
      :value="value"
      class="plain-input__input"
      @input="$emit('input', $event.target.value)"
      @change="$emit('change', $event.target.value)"
    />
    <span class="plain-input__error">
      {{ errorText }}
    </span>
  </div>
</template>

<script>
import Icon from 'common/components/Icon.vue';
import autosize from 'autosize';

export default {
  name: 'PlainInput',
  components: { Icon },
  props: {
    value: [String, Number],
    type: {
      type: String,
      default: 'text'
    },
    label: String,
    name: String,
    errorText: String,
    required: Boolean,
    masked: Boolean,
    disabled: Boolean,
    isPhone: Boolean
  },
  data() {
    return {
      isVisited: false,
      inputType: this.type,
      isFocused: false
    };
  },
  computed: {
    isError() {
      return this.errorText && this.isVisited;
    },
    isPassword() {
      return this.inputType === 'password';
    }
  },
  mounted() {
    if (this.type === 'textarea') {
      autosize(this.$refs.textarea);
    }
  },
  methods: {
    bluredEvent(e) {
      this.isVisited = true;
      this.isFocused = false;
      if (this.isPhone) this.onBlurPhoneField();
      this.$emit('blur');
    },
    toggleType() {
      this.inputType = this.isPassword ? 'text' : 'password';
    },
    onFocus() {
      if (this.isPhone) this.onFocusPhoneField();
      this.$emit('focus');
      this.isFocused = true;
    },
    onFocusPhoneField() {
      if (!this.value) this.$emit('input', '+7 (');
    },
    onBlurPhoneField() {
      if (this.value.length <= 4) this.$emit('input', '');
    }
  }
};
</script>

<style lang="scss" scoped>
