    width: 100%;
    height: 2px;
    background-color: #009ce9;
    transform: scaleX(0);
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
  }
  &:not(.disabled):hover,
  &:not(.disabled).focused {
    .plain-input__label {
      color: #009ce9;
    }
    &:after {
      transform: scaleX(1);
    }
  }
  &--error,
  &--error:hover,
  &--error:not(.disabled):hover,
  &--error.disabled:hover,
  &--error:not(.disabled),
  &--error.focused:not(.disabled),
  &--error.focused:not(.disabled):hover {
    .plain-input__input {
      box-shadow: none;
    }
    .plain-input__error {
      opacity: 1;
    }
    .plain-input__label {
      color: #ff3535;
    }
    &:after {
      background-color: #ff3535;
    }
  }
}

