import axios from 'common/helpers/httpService';
import { setField } from 'common/store/helpers';

const state = {
  authorized: null,
  phone: '',
  mqttPassword: '',
  cid: '',
  roles: [],
  yurType: '',
  isAdmin: false,
  clientId: `mqtt_${Math.random()
    .toString(16)
    .substr(2, 8)}`,
  fullData: null,
  category: null,
  isTester: false
};

const getters = {
  isShipper(state) {
    return state.category === 'shipper';
  }
};

const actions = {
  login({ commit }, data) {
    commit('login', data);
  },
  async loginWithToken({ state, commit, rootState: { defaultURL } }, token) {
    const data = await axios.post(`/account/login`, `cookie=${token}&clientId=${state.clientId}`);
    localStorage.setItem('sessionKey', data.cookie);
    if (data.phase === 'initial') {
      return data;
    }
    commit('login', { ...data, clientId: state.clientId });
    return null;
  },
  logout({ commit, dispatch, rootGetters: { client } }) {
    localStorage.removeItem('sessionKey');
    if (client) dispatch('common/closeSession', null, { root: true });
    commit('logout');
  },
  setYurType({ commit }, val) {
    commit('setYurType', val);
  },
  setFieldIsAdmin({ commit }, value) {
    commit('setField', { field: 'isAdmin', value });
  }
};

const mutations = {
  setField,
  login(state, response) {
    state.phone = response.mqtt_login;
    state.authorized = true;
    state.mqttPassword = response.mqtt_password;
    state.cid = response.cid;
    state.roles = response.roles;
    state.isAdmin = response.is_admin.toString() === 'true';
    state.yurType = (response.entity && response.entity.toUpperCase()) || '';
    state.category = response.category;
    const flag = response.debug_phone_flag;
    state.isTester = typeof flag === 'boolean' ? flag : flag === 'true';
    state.fullData = response;
    state.clientId = response.clientId;
  },
  logout(state) {
    state.authorized = false;
    state.phone = '';
    state.mqttPassword = '';
    state.cid = '';
    state.roles = [];
    state.yurType = '';
    state.isAdmin = false;
  },
  setYurType(state, val) {
    state.yurType = val;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
};
