<template>
  <div>
    <nuxt />
  </div>
</template>

<script>
export default {
  async fetch() {
    await this.$store.dispatch('settings/getSettings');
  },
  created() {
    this.gtmIsEnabled = process.env.VUE_APP_GTM_ENABLED === 'true';
  },
  mounted() {
    this.checkBrowserOnWebKit();
  },
  methods: {
    checkBrowserOnWebKit() {
      if (window.webkitURL === null) {
        return;
      }
      document.body.classList.add('webkit');
    }
  }
};
</script>

<style lang="scss">
@import '../assets/sass/main.scss';
</style>
