const errorMap = {
  E_SMSOVERLOAD: 'Нет связи с сервером, попробуйте позже',
  E_BADPROMO: 'Промокод неверен',
  E_ENGAGED: 'Такой пользователь уже существует',
  E_BADPHONE: 'Неверный логин или пароль',
  E_BADPHONELENGTH: 'Неверный номер телефона',
  E_BADPASSWORD: 'Неверный логин или пароль',
  E_EXHAUSTED:
    'Превышен суточный лимит запросов кода подтверждения для этого номера телефона',
  E_INTENSIVE:
    'Вы не можете запрашивать код подтверждения для этого номера телефона чаще, чем раз в минуту',
  E_UNKNOWNUSER: {
    email: 'Такой email не существует или не был верифицирован',
    phone: 'Пользователь с таким телефоном не найден'
  },
  E_MISMATCH: 'Неверный код подтверждения',
  E_ATTEMPTS: 'Превышен лимит попыток ввода кода подтверждения',
  E_BADEMAIL: 'Неверный адрес электронной почты',
  E_IP_LIMIT_SENDING:
    'Вы можете отправлять не больше одного сообщения в минуту!',
  E_LIMIT:
    'Количество попыток регистрации для введенного номера превышено. Обратитесь в поддержку.',
  default: 'Произошла ошибка, попробуйте еще раз'
};

const getters = {
  getError: () => ({ error, type = null }) =>
    (type && errorMap[error]?.[type]) ||
    (typeof errorMap[error] === 'string' && errorMap[error]) ||
    errorMap.default
};

export default {
  namespaced: true,
  getters
};
