import Vuex from 'vuex';
import login from 'common/store/login';
import settings from 'common/store/settings';
import { setField } from 'common/store/helpers';
import errors from './modules/errors';

const defaultURL = `${process.env.VUE_APP_WEB_PROTOCOL}://${
  process.env.VUE_APP_WEB_HOST
}${(process.env.VUE_APP_WEB_PORT && `:${process.env.VUE_APP_WEB_PORT}`) || ''}`;

const createStore = () => {
  return new Vuex.Store({
    state: () => ({
      defaultURL,
      showSignInDialog: false,
      showSignUpDialog: false
    }),
    mutations: {
      showDialog(state, name) {
        state.showSignInDialog = name === 'SignIn';
        state.showSignUpDialog = name === 'SignUp';
      },
      toggleAuth(state) {
        state.showSignInDialog = !state.showSignInDialog;
        state.showSignUpDialog = !state.showSignUpDialog;
      },
      setField
    },
    modules: {
      errors,
      login,
      settings
    }
  });
};

export default createStore;
